.btn {
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 1.6rem;
  color: var(--text-color-secondary);
  border-style: none;
  background-color: var(--bg-color-dark);
  padding: 1rem;
  box-shadow: var(--drop-shadow-sm);
  border-radius: 3px;
  transition: 0.2s;
}
.btn__reset {
  aspect-ratio: 1/1;
}
.btn:hover {
  background-color: var(--bg-color-mid);
  color: var(--text-color-primary);
  transform: translateY(-2px);
  cursor: pointer;
  box-shadow: var(--drop-shadow-sm-1);
}
.btn:active {
  transform: translateY(3px);
  box-shadow: var(--drop-shadow-sm-2);
}

.update {
  color: aquamarine;
  position: absolute;
  top: 25.4rem;
  width: 70%;
  align-self: center;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "regofont";
  src: url(../assets/regofont.ttf) format("truetype");
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media screen and (max-width: 600px) {
  html {
    font-size: 50%;
  }
}
body {
  font-family: Arial, Helvetica, sans-serif, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #333;
  background-image: linear-gradient(112.96deg, #ffec9f 10.11%, #c9a4db 88.12%);
  display: grid;
  grid-template-columns: 1fr minmax(300px, 1200px) 1fr;
  grid-template-rows: 6rem auto auto;
  min-height: 100vh;
}

main {
  grid-column: 2/3;
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(200px);
  border-radius: 15px;
  border: 1px solid rgba(100, 100, 100, 0.247);
  position: relative;
  box-shadow: var(--drop-shadow-lg);
  z-index: 2;
}

.page__header {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  border: 1px solid rgba(100, 100, 100, 0.247);
  padding: 1rem;
  grid-column: 2/3;
  line-height: 1.2;
  border-radius: 20px;
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(600px);
  box-shadow: var(--drop-shadow-lg);
  z-index: 1;
}

:root {
  --text-color-primary: #494949;
  --text-color-secondary: #ff8383;
  --bg-color-light: #f2f2f2;
  --bg-color-dark: #494949;
  --bg-color-mid: #d0d0d0;
  --art-accent-clr: #fff;
  --art-background-clr: #000;
  --gradient-color-primary: linear-gradient(
  	94.57deg,
  	#d9d9d9 33.34%,
  	#e6e6e6 50.04%,
  	#c2c0c0 65.67%
  );
  --drop-shadow-lg: 0px 100px 80px rgba(0, 0, 0, 0.07),
  	0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198),
  	0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275),
  	0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035),
  	0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725),
  	0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802);
  --drop-shadow-sm: 0px 0px 10px rgba(0, 0, 0, 0.1);
  --drop-shadow-sm-1: 5px 10px 10px rgba(0, 0, 0, 0.3);
  --drop-shadow-sm-2: 2px 2px 5px rgba(0, 0, 0, 0.3);
  --drop-shadow-md: 0px 100px 80px rgba(0, 0, 0, 0.0196802),
  	0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0282725),
  	0px 22.3363px 17.869px rgba(0, 0, 0, 0.035),
  	0px 12.5216px 10.0172px rgba(0, 0, 0, 0.0417275),
  	0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0503198),
  	0px 2.76726px 2.21381px rgba(0, 0, 0, 0.07);
  --text-color-black: #000;
  --text-color-white: #fff;
  --text-color-red: #ff0000;
  --text-color-green: #00ff00;
  --text-color-blue: #0000ff;
  --text-color-yellow: #ffff00;
  --text-color-pink: #ff00ff;
  --bg-color-black: #000;
  --bg-color-white: #fff;
  --bg-color-red: #ff0000;
  --bg-color-green: #00ff00;
  --bg-color-blue: #0000ff;
  --bg-color-yellow: #ffff00;
  --bg-color-pink: #ff00ff;
}

.container__grid {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(3, auto);
  grid-gap: 1rem;
  justify-items: stretch;
  padding: 1rem;
}
.container__grid__item--col-styles {
  position: relative;
  grid-column: 1/span 1;
  grid-row: 1/span 1;
  z-index: 1;
}
.container__grid__item--artboard {
  position: relative;
  z-index: 10;
  grid-column: 1/span 1;
  grid-row: 2/span 1;
}
.container__grid__item--text {
  position: relative;
  z-index: 11;
  grid-column: 1/span 1;
  grid-row: 3/span 1;
}

@media screen and (min-width: 600px) {
  .container__grid {
    grid-template-columns: 1fr 1fr;
  }
  .container__grid__item--col-styles {
    position: relative;
    grid-column: 1/span 2;
    grid-row: 2/span 1;
    z-index: 1;
  }
  .container__grid__item--artboard {
    position: relative;
    z-index: 10;
    grid-row: 1/span 1;
    grid-column: 1/span 2;
  }
  .container__grid__item--text {
    position: relative;
    z-index: 11;
    grid-column: 1/span 2;
    grid-row: 3/span 1;
  }
}
.controls__text {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

@media screen and (min-width: 800px) {
  .controls__text {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }
}
footer {
  display: flex;
  justify-content: center;
  grid-row: 3;
  grid-column: 2;
  margin-top: auto;
}

.artboard {
  background-color: #ffffff;
  min-height: 15rem;
  border-radius: 5px;
  box-shadow: var(--drop-shadow-lg);
  margin-inline: auto;
  display: grid;
  place-items: center;
}

@media screen and (min-width: 600px) {
  .artboard {
    min-height: 30rem;
  }
}
.art {
  overflow: hidden;
  font-family: "regofont";
  position: relative;
  min-width: 270px;
  aspect-ratio: 3.14;
  border-radius: 10px;
  text-align: center;
  background-color: #000;
  justify-content: center;
}
.art .outter__line {
  width: 266px;
  height: 82px;
  border-radius: 9px;
}
.art .inner__line {
  width: 260px;
  height: 76px;
  border-radius: 7px;
}
.art .outter__line,
.art .inner__line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid var(--art-accent-clr);
}

@media screen and (min-width: 600px) {
  .art {
    scale: 2;
  }
}
.text__content {
  color: var(--art-accent-clr);
  place-self: center;
  text-transform: uppercase;
  position: absolute;
}
.text__content__middle {
  font-size: 4rem;
  left: 15%;
  top: 25%;
  white-space: nowrap;
}
.text__content__top {
  left: 43%;
  top: 10%;
  letter-spacing: 3px;
  white-space: nowrap;
}
.text__content__left {
  display: flex;
  word-break: break-all;
  text-transform: uppercase;
  text-align: center;
  width: 0.2em;
  left: 5%;
  top: 28%;
}
.text__content__bottom {
  left: 35%;
  bottom: -1%;
  letter-spacing: 3px;
  padding-bottom: 0.5rem;
  white-space: nowrap;
}

.controls__input__container {
  width: 100%;
  grid-column: 1/-1;
  grid-row: 1/span 1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 1.5rem;
  align-items: center;
}

@media screen and (min-width: 600px) {
  .controls__input__container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    align-items: stretch;
  }
}
.controls__input {
  padding: 1.6rem;
  font-size: 1.6rem;
  border-radius: 5px;
  border: 1px solid rgba(100, 100, 100, 0.247);
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(200px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.controls__input--colour {
  grid-row: 1/span 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1.5rem;
}
.controls__input--style {
  grid-row: 2/span 1;
}
.controls__input--text4 {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1.5rem;
}
.controls__input .form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.controls__input .form .label {
  margin-right: auto;
}
.controls__input .form input {
  position: relative;
  margin: 0.8rem;
  cursor: pointer;
}

@media screen and (min-width: 600px) {
  .controls__input--colour {
    grid-column: 1/span 1;
    grid-row: 1/span 1;
  }
  .controls__input--style {
    grid-column: 2/-1;
    grid-row: 1/span 1;
  }
  .controls__input--text4 {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 1.5rem;
  }
}
.middle__txt--container,
.side__txt--container,
.top__txt--container,
.bottom__txt--container {
  grid-column: 3/-1;
  grid-row: 1/span 2;
  position: relative;
}

.controls__text--input__field {
  padding: 1rem;
  box-shadow: var(--drop-shadow-md);
  border: none;
  width: 100%;
}

.font__block {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 1.4rem;
  color: var(--text-color-primary);
}
.font__block--size__wrapper {
  grid-column: 1/3;
  grid-row: 1/span 2;
}
.font__block--posx__wrapper {
  grid-column: 1/3;
  grid-row: 3/span 2;
}
.font__block--posy__wrapper {
  grid-column: 3/-1;
  grid-row: 3/span 2;
}

@media screen and (min-width: 600px) {
  .font__block--size__wrapper {
    grid-column: 1/3;
    grid-row: 2/span 1;
  }
  .font__block--posx__wrapper {
    grid-column: 3/5;
    grid-row: 2/span 1;
  }
  .font__block--posy__wrapper {
    grid-column: 5/-1;
    grid-row: 2/span 1;
  }
  .middle__txt--container,
.side__txt--container,
.top__txt--container,
.bottom__txt--container {
    grid-column: 1/-1;
    grid-row: 1/span 1;
  }
}
.controls__text__selector {
  margin: 0 0.5rem 0 0;
  width: auto;
  padding: 1rem;
}

.btn__clear__txt {
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: var(--bg-color-mid);
  border-style: none;
  border-radius: 50%;
  height: 13px;
  aspect-ratio: 1/1;
  transition: 0.2s;
}
.btn__clear__txt span {
  font-size: 1rem;
  transform: translate(0.3px, -1.1px);
}
.btn__clear__txt:hover {
  background-color: var(--bg-color-mid);
  color: var(--text-color-primary);
  transform: translateY(calc(-50% - 0.5px));
  cursor: pointer;
  box-shadow: var(--drop-shadow-sm);
}
.btn__clear__txt:active {
  transform: translateY(calc(-50% + 0.2px));
  box-shadow: var(--drop-shadow-sm);
}

@media screen and (min-width: 600px) {
  .btn__clear__txt {
    position: absolute;
    right: 2%;
  }
}
